@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Rubik:700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.button{background:none;border:0;position:relative;cursor:pointer;text-transform:uppercase;outline:none;vertical-align:middle;text-decoration:none;font-size:inherit;font-family:serif;font-family:initial}.button_add{background-color:#fff0f0;border:2px solid #b18597;border-radius:999px;font-size:1.7rem;height:100px;position:relative;opacity:0.7;width:100px}.button_add::after,.button_add::before{content:"";display:block;background-color:grey;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.button_add::before{height:1em;width:0.2em}.button_add::after{height:0.2em;width:1em}.button_add:hover{opacity:1}.button_close{outline:0;color:#010e1b;text-transform:uppercase;letter-spacing:2px}.button_close:hover{color:#065baf}.button_delete{letter-spacing:2px;color:red;font-weight:bold}.button_delete:hover{color:#e18787}.button_3d{display:block;margin:auto;margin-top:4vh;margin-bottom:10px;padding:1.25em 2em;border:2px solid #b18597;border-radius:0.75em;font-family:inherit;font-weight:700;color:#382b22;text-transform:uppercase;background:#fff0f0;transform-style:preserve-3d;transition:0.15s cubic-bezier(0, 0, 0.6, 1)}.button_3d::before{content:'';position:absolute;top:0;right:0;bottom:0;left:0;width:100%;height:100%;border-radius:inherit;background:#f9c4d2;box-shadow:0 0 0 2px #b18597, 0 .625em 0 0 #ffe3e2;transform:translate3d(0, 0.75em, -1em);transition:0.15s cubic-bezier(0, 0, 0.6, 1)}.button_3d:hover{background:#ffe9e9;transform:translate(0, 0.25em)}.button_3d:hover::before{box-shadow:0 0 0 2px #b18597, 0 .5em 0 0 #ffe3e2;transform:translate3d(0, 0.5em, -1em)}.button_3d:active{background:#ffe9e9;transform:translate(0, 0.75em)}.button_3d:active::before{box-shadow:0 0 0 2px #b18597, 0 0 #ffe3e2;transform:translate3d(0, 0, -1em)}.image{max-width:100%;max-height:96vh}.input_file{display:none}.input_text{height:35px;width:96%}.input_text::-webkit-input-placeholder{text-align:center}.input_text:-ms-input-placeholder{text-align:center}.input_text::-ms-input-placeholder{text-align:center}.input_text::placeholder{text-align:center}.main{background-color:#faf4f6;align-items:center;justify-content:center}.main__container{max-width:860px;min-width:320px;display:flex;flex-wrap:wrap;margin:auto}.main__container::after{content:'';flex-grow:100}.main__button_add{margin:auto}.main__image{width:100%;vertical-align:bottom}.main__image-wrapper{background-image:url("https://via.placeholder.com/120");background-position:center;background-repeat:no-repeat;height:120px;margin:2px;width:120px}@media (min-width: 480px){.main__image-wrapper{background-image:url("https://via.placeholder.com/200");height:200px;width:200px}}.popup{font-family:serif;font-family:initial;display:flex;position:fixed;justify-content:center;align-items:center;width:100%;height:100%;background:rgba(250,244,246,0.7);top:0;right:0;bottom:0;left:0;z-index:1}.popup__inner{background:#faf4f6;border:5px solid #ead1d9;padding:20px;position:relative;opacity:1;max-height:100%;box-sizing:border-box}.popup__button{position:absolute}.popup__button_close{top:3px;right:10px}.popup__button_delete{top:3px;left:10px}.form{background-color:#faf4f6;border:10px dashed #ead1d9;border-radius:10px;position:fixed;width:90vw;height:90vh;margin:auto;min-width:280px;top:0;right:0;bottom:0;left:0;z-index:1}.form_empty{position:static}.form__button_close{position:absolute;top:3px;right:10px}.form__input_text{display:block;margin:auto;margin-top:6vh}.form__label_input{cursor:pointer;color:blue}.form__label_input:hover{color:#33f}.form__label_input:active{color:#ead1d9}.form__p{text-align:center;font-family:'Caveat', cursive;font-size:3rem}@media (min-width: 420px){.form__p{margin-top:40px}}.form__p_second{margin-bottom:10px}.form__p_third{margin-top:0px}

